<template>
  <div>
    <v-card v-if="loading">
      <v-progress-linear  :active="loading" :indeterminate="loading" absolute top color="primary"></v-progress-linear>
    </v-card>
    
    <v-card v-if="!loading">
      <v-card-text class="py-0">
        <v-timeline align-top dense v-for="(match) in matches" :key="match.index">
          <v-timeline-item small color="warning">
            <v-row class="pt-1">
              <v-col cols="3">
                <strong>{{match.matchTime}}</strong>
              </v-col>
              <v-col cols="7">
                <strong>{{match.homeTeam}}</strong>
                <div
                  v-for="driver in match.driverIDs"
                  :key="driver._id"
                  class="caption"
                >{{driver.name}}</div>
              </v-col>
              <v-col cols="2" class="away-uniform" v-if="match.awayUniform === true">
                <v-icon color="#ffd907">mdi-tshirt-crew</v-icon>
              </v-col>
            </v-row>
            <v-select
            class="d-block"
            clearable
            :items="players"
            item-text="name"
            item-value="_id"
            :menu-props="{ maxHeight: '300' }"
            label="Select"
            multiple
            single-line
            hint="Rijders toevoegen"
            v-model="match.selectedPlayers"
            ></v-select>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              small
              color="primary"
              @click="addDriver(match._id, match.selectedPlayers);"
            >Opslaan</v-btn>
            </v-card-actions>
          </v-timeline-item>
        </v-timeline>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";

export default {
  data: () => ({
    loading: true,
    selectedPlayers: [],
  }),
  computed: {
    ...mapState(["players", "matches"]),
  },
  created() {
    this.$store.dispatch("getPlayers");
    this.$store.dispatch("getMatches");
    this.loading = false;
    
  },
  methods: {
    addDriver(matchId, drivers) {
      axios
        .put(`http://localhost:3000/api/matches/${matchId}`, {
          driverIDs: drivers,
        })
        .then((response) => {
          this.$store.dispatch("getMatches", response.data);
        });
    },
  },
};
</script>

<style scoped>
.col {
  padding: 0;
}
.away-uniform {
  text-align: center;
}
</style>
