<template>
  <div>
    <v-subheader class="page_title">Login</v-subheader>
    <v-card class="pa-3">
      <v-alert
        :value="true"
        type="error"
        v-if="error && !success"
      >Er was een fout, niet in staat om de login te voltooien</v-alert>
      <v-form>
        <v-text-field type="text" v-model="user.email" label="E-mail"></v-text-field>
        <v-text-field type="password" v-model="user.password" label="Password"></v-text-field>
        <v-btn @click.prevent="loginUser" color="primary">Login</v-btn>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import axios from "axios"
import { mapActions } from "vuex"
export default {
  data() {
    return {
      user:{
        email: "",
        password: "",
        
      },
      error: false
    };
  },
  methods: {
    async loginUser() {
      try {
        let response = await axios.post("http://localhost:3000/api/user/login", this.user);
        let token = response.data.token;
        localStorage.setItem("auth-token", token);
        if (token) {
          console.log("Success", "Login Successful", "success");
          axios.defaults.headers.common['auth-token'] = token;
          this.$router.push("/");
        }
      } catch (err) {
        this.error = true
        console.log(err.response);
      }
    }
  }
};
</script>

<style lang="sass">
.page_title
  padding: 0
</style>